import { ArrowIcon } from '@fdc/vsa-react-lib';
import React, { ButtonHTMLAttributes, MouseEvent, ReactNode, useRef, useState } from 'react';
import { useOutsideClick } from '../../hooks';
import Button from '../Button';
import styles from './DropdownButton.module.scss';

interface IDropdownButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  label: string;
  isSecondary?: boolean;
  labelClassName?: string;
  buttonStyle?: string;
  disabled?: boolean;
  title?: string;
  size?: 'small' | 'medium' | 'large';
  mode?: 'primary' | 'secondary' | 'transparent';
  children?: ReactNode;
}

/**
 * Компонент кнопки с выпадающим контентом
 */
const DropdownButton = (props: IDropdownButtonProps) => {
  const { children, onClick, ...buttonProps } = props;
  const { buttonStyle, className, label } = props;

  const [isOpen, setIsOpen] = useState<boolean>(false);

  const ref = useRef(null);
  useOutsideClick(ref, () => setIsOpen(false));

  const handleClick = (e: MouseEvent<HTMLButtonElement>) => {
    onClick && onClick(e);
    setIsOpen(!isOpen);
  };

  return (
    <div ref={ref} className={`${styles.container} ${buttonStyle} ${className}`}>
      <Button
        {...buttonProps}
        rightIcon={<ArrowIcon className={`${styles.icon} ${isOpen && styles.opened}`} />}
        onClick={handleClick}
      >
        {label}
      </Button>
      {isOpen && children && (
        <div className={styles.dropdownWrapper}>
          <div className={styles.dropdown}>{children}</div>
        </div>
      )}
    </div>
  );
};

export default DropdownButton;
