import React, { PureComponent } from 'react';
import styles from './Risk.module.scss';
import { ReactComponent as ShowIcon } from '../../../assets/show.svg';
import { ReactComponent as HideIcon } from '../../../assets/hide.svg';

import { ReactComponent as NegativeIcon } from '../../../assets/negative.svg';
import { ReactComponent as ResortingIcon } from '../../../assets/resorting.svg';
import { ReactComponent as GapIcon } from '../../../assets/gap.svg';
import { ReactComponent as HighCostIcon } from '../../../assets/highCost.svg';
import { ReactComponent as LowCostIcon } from '../../../assets/lowCost.svg';

import { GraphStoreType } from '../../../../../../stores/graph/graphStore';
import { inject, observer } from 'mobx-react';

import { formatMoney } from '../../../../../../utils/ui/formatMoney';
import { formatDateIndividualPerson } from '../../../../../../utils/ui/formatDateIndividualPerson';

type RiskProps = {
  graphStore?: GraphStoreType;
  risk?: any;
};

type RiskState = {
  onShow: any;
};

/**
 * Компонент основной информации узла
 */
@inject('graphStore')
@observer
class Risk extends PureComponent<RiskProps, RiskState> {
  /**
   * Метод сохраняет данные о НП в состояние компонента
   */
  constructor(props: RiskProps) {
    super(props);

    this.state = {
      onShow: false,
    };
  }

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const {
      props: { risk },
      state: { onShow },
    } = this;

    return (
      <div className={styles.container}>
        <div
          onClick={() => this.setState({ onShow: !onShow })}
          className={onShow ? `${styles.headers} ${styles.active}` : `${styles.headers}`}
        >
          <span className={styles.arrow}>
            {onShow ? (
              <ShowIcon className={styles.showHide} />
            ) : (
              <HideIcon className={styles.showHide} />
            )}
          </span>
          <span className={styles.codeName}>
            {risk.code === '201' && <NegativeIcon className={styles.icon} />}
            {risk.code === '202' && <GapIcon className={styles.icon} />}
            {risk.code === '204' && <HighCostIcon className={styles.icon} />}
            {risk.code === '206' && <ResortingIcon className={styles.icon} />}
            {risk.code === '207' && <LowCostIcon className={styles.icon} />}
            {risk.code_name}
          </span>
          <span>{risk.code_description}</span>
          <span className={styles.damageSum}>{formatMoney(risk.damage_sum)}</span>
        </div>
        {onShow && (
          <div className={styles.tableContainer}>
            <table className={styles.table}>
              <thead className={styles.tableHeader}>
                <tr>
                  <td>Товарная группа</td>
                  <td>Кол-во, шт.</td>
                  <td>НС</td>
                  <td>Потенциальный ущерб, ₽</td>
                  {risk.code === '201' || risk.code === '206' ? <td>Остаток</td> : ''}
                  {risk.code === '204' || risk.code === '207' ? <td>% Отклонения</td> : ''}
                  <td>Дата выявления</td>
                  <td>Дата закрытия</td>
                </tr>
              </thead>
              <tbody>
                {risk.products?.map((item: any, index: number) => (
                  <tr className={styles.tbodyRow}>
                    <td>{item.tnved_group}</td>
                    <td>{item.product_num + ' ' + (item.unit_name ?? '')}</td>
                    <td>{item.tax_component === '1' ? 'Да' : 'Нет'}</td>
                    <td>{formatMoney(item.damage)}</td>
                    {risk.code === '201' || risk.code === '206' ? <td>{item.balance}</td> : ''}
                    {risk.code === '204' || risk.code === '207' ? <td>{item.otk}</td> : ''}
                    <td>{formatDateIndividualPerson(item.opened)}</td>
                    <td>{formatDateIndividualPerson(item.closed)}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    );
  }
}

export default Risk;
