import React from 'react';
import { ITextareaProps } from './Textarea.props';
import styles from './Textarea.module.scss';

const Textarea = ({
  className,
  label,
  labelPosition = 'left',
  textareaStyle,
  ...props
}: ITextareaProps) => {
  return (
    <label className={`${styles.wrapper} ${className} ${styles[labelPosition]}`}>
      {label && <span className={`${styles.label}`}>{label}</span>}
      <textarea className={`${styles.textarea} ${textareaStyle}`} {...props} />
    </label>
  );
};

export default Textarea;
