import React, { useState, MouseEvent, ChangeEvent, DragEvent, useRef } from 'react';
import { useStores } from '../../hooks';

import { IFileUploaderProps, IFile } from './FileUploader.props';

import Button from '../Button';
import Icon from '../Icon';

import { ReactComponent as FileIcon } from './assets/file.svg';
import { ReactComponent as ErrorIcon } from './assets/error.svg';

import styles from './FileUploader.module.scss';

const FileItem = ({
  file,
  onItemRemove,
  getUploadedDocument,
  appTheme,
  disabled,
}: {
  file: IFile;
  onItemRemove: (e: MouseEvent, name: string) => void;
  getUploadedDocument: (documentId: string, fileName: string) => void;
  appTheme: string;
  disabled?: boolean;
}) => {
  const { document_id, file_name } = file;

  return (
    <div
      className={styles.file}
      onClick={() => (document_id ? getUploadedDocument(document_id, file_name) : false)}
    >
      <div className={styles.file__title}>
        <FileIcon className={styles.fileIcon} />
        <span>{file_name}</span>
      </div>

      {!disabled && (
        <Icon
          className={styles.file__delete}
          name="close"
          onClick={(e: MouseEvent) => onItemRemove(e, file_name)}
          theme={appTheme}
        />
      )}
    </div>
  );
};

const FileUploader = ({
  className,
  fileList = [],
  onFilesChange,
  disabled,
  buttonStyle,
  ...props
}: IFileUploaderProps) => {
  const commonAccept = `.xls,.xlsx,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel.doc,
    .docx,application/vnd.openxmlformats-officedocument.wordprocessingml.document,application/msword.pdf,application/pdf,.txt,.rft`;

  const allowTypes = {
    rtf: true,
    doc: true,
    docx: true,
    pdf: true,
    xls: true,
    xlsx: true,
    txt: true,
  };

  const {
    commonStore: { appTheme },
    graphStore: { getUploadedDocument },
  } = useStores();

  const inputRef = useRef<HTMLInputElement>(null);

  const [dragActive, setDragActive] = useState<boolean>(false);
  const [errors, setErrors] = useState<string[]>([]);

  const attachFiles = (uploadedfiles: File[]) => {
    const invalidFiles: string[] = [];

    const streams: IFile[] = uploadedfiles
      .map((file: File) => ({
        stream: file,
        file_name: file.name,
      }))
      .filter(({ file_name }) => {
        const fileExt = file_name.split('.').pop() as string;

        if (fileExt in allowTypes) return true;

        invalidFiles.push(file_name);
      });

    onFilesChange([...fileList, ...streams]);
    setErrors([...invalidFiles]);
  };

  const onItemRemove = (e: MouseEvent, name: string): void => {
    e.stopPropagation();

    const filteredFiles = fileList.filter((item) => item.file_name !== name);
    onFilesChange(filteredFiles);
  };

  const onDrag = (e: DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();

    if (e.type === 'dragenter' || e.type === 'dragover') {
      setDragActive(true);
    } else if (e.type === 'dragleave') {
      setDragActive(false);
    }
  };

  const onChangeFiles = (e: ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();

    const files = e.target.files ? [...e.target.files] : [];
    attachFiles(files);
  };

  const onDropFiles = (e: DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();

    const files = e.dataTransfer.files ? [...e.dataTransfer.files] : [];
    attachFiles(files);

    setDragActive(false);
  };

  const onButtonClick = (e: MouseEvent) => {
    e.preventDefault();

    inputRef.current?.click();
  };

  return (
    <div className={`${styles.container} ${className}`} {...props}>
      <input
        type="file"
        accept={commonAccept}
        ref={inputRef}
        onChange={onChangeFiles}
        multiple
        style={{ display: 'none' }}
      />

      {!disabled && (
        <>
          <span className={styles.label}>Добавить файлы</span>

          <div
            className={`${styles.field} ${dragActive && styles.active}`}
            onDragEnter={onDrag}
            onDragLeave={onDrag}
            onDragOver={onDrag}
            onDrop={onDropFiles}
          >
            <Button className={buttonStyle} onClick={onButtonClick} mode="secondary">
              Выберите файлы
            </Button>

            <span className={styles.title}>Перетащите сюда файлы для загрузки</span>
          </div>
        </>
      )}

      {fileList.length > 0 && (
        <div className={styles.list}>
          {fileList.map((file) => (
            <FileItem
              key={'file-list-item-' + file.file_name}
              file={file}
              onItemRemove={onItemRemove}
              getUploadedDocument={getUploadedDocument}
              appTheme={appTheme}
              disabled={disabled}
            />
          ))}
        </div>
      )}

      <div className={styles.errors}>
        {errors.map((error) => (
          <div key={`fileError${error}`} className={styles.error}>
            <ErrorIcon className={`${styles.error__icon}`} />
            Указанный файл {error} имеет неподдерживаемый формат или испорчен. Укажите корректный
            файл в одном из форматов: .rtf, .doc, .docx, .pdf, .xls, .xlsx, .txt.
          </div>
        ))}
      </div>
    </div>
  );
};

export default FileUploader;
