import React, { PureComponent } from 'react';

import { StatusesIcons } from 'components/Icon';
import { formatDateDetailedLink } from 'utils/ui/formatDateDetailedLink';

import styles from './ChangeHistory.module.scss';

interface IHistory {
  change_date: Date;
  change_type: 'N' | 'K' | 'U' | 'I';
  change_text: string;
  user_name: string;
  vertex_role_type: string;
}

interface IChangeHistoryProps {
  data: IHistory[];
}

class ChangeHistory extends PureComponent<IChangeHistoryProps> {
  render() {
    const { data } = this.props;

    return (
      <div className={styles.data_container}>
        <div className={styles.dataHeader}>
          <span className={styles.headerLabel}>История изменений</span>
        </div>

        <table className={styles.data_table} cellSpacing="0" cellPadding="0">
          <thead className={styles.data_table_header}>
            <tr>
              <td>Дата и время</td>
              <td>Кем изменено</td>
              <td>Роль</td>
              <td>Изменения</td>
            </tr>
          </thead>
          <tbody className={styles.data_table_tbody}>
            {data.map((history) => (
              <tr>
                <td className={styles.date_cell}>
                  {formatDateDetailedLink(new Date(history.change_date).toISOString())}
                </td>
                <td className={styles.name_cell}>{history.user_name}</td>
                <td className={styles.role_cell}>{history.vertex_role_type || '-'}</td>
                <td className={styles.text_cell}>{history.change_text}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
}

export default ChangeHistory;
