import { observer } from 'mobx-react';
import React, { useEffect, useState } from 'react';
import { useStores } from '../../../../../hooks';

import RoleForm from '../RoleForm';
import RolesHeader from '../RolesHeader';
import RolesItem from '../RolesItem';

import { IRole } from '../RoleForm/RoleForm.props';

import { Mode } from 'constants/mode';
import styles from './BusinessRole.module.scss';

interface IBusinessRoleProps {
  disabled?: boolean;
  nodeData: any;
  changeRole: (information: object) => void;
}

const BusinessRole = observer(({ disabled, nodeData, changeRole }: IBusinessRoleProps) => {
  const {
    commonStore: {
      businessRolesIcons,
      getBusinessRoleByBrief,
      getBusinessRoleByName,
      getChangeReasonByName,
      businessRolesList,
      changeReasonsList,
    },
    detailedNodeStore: { businessRole },
    graphStore: { showChangeNP, showReasons },
  } = useStores();

  const [opened, setOpened] = useState<boolean>(false);
  const [editing, setEditing] = useState<boolean>(false);

  const onSave = (data: IRole) => {
    changeRole({
      biz_role_name: data.name,
      biz_role_id: getBusinessRoleByName(data.name)?.role_id || null,
      biz_role_description: data.comment,
      biz_role_change_reason: getChangeReasonByName(data.reason)?.reason_id || null,
      biz_role_info_source: data.source,
      attach_docs_biz_role: data.files,
    });
  };

  const formOptions = {
    roleList: businessRolesList.map(({ value }: { value: string }) => value),
    reasonList: changeReasonsList.map(({ name }: { name: string }) => name),
    roleLabel: 'Наименование бизнес роли',
    showRoleLabel: 'Просмотр бизнес роли',
    rolePlaceHolder: 'Укажите наименование бизнес роли',
    reasonDisabled: !showReasons,
    onSave,
  };

  const rolesArray = nodeData?.biz_roles_array.filter((item: any) => item);
  const hasRoles = !!businessRole?.name;

  const [mode, setMode] = useState(Mode.CREATE);

  useEffect(() => setMode(hasRoles ? Mode.EDIT : Mode.CREATE), [hasRoles]);

  return (
    <div className={styles.container}>
      <RolesHeader
        title="Бизнес роли налогоплательщика"
        roleName="бизнес-роль"
        count={rolesArray?.length || 0}
        hasRoles={hasRoles}
        setOpened={() => {
          setOpened((e) => !e);
          setEditing(true);
        }}
        canEdit={showChangeNP}
      >
        <RoleForm options={{ ...formOptions, saveButtonText: 'Добавить' }} mode={mode} />
      </RolesHeader>
      {rolesArray?.map((item: any, i: number) => {
        const title = getBusinessRoleByBrief(item)?.value || '';
        const icon = businessRolesIcons?.[item]?.icon || '';
        return (
          <RolesItem
            key={`businessRolesItem${i}`}
            icon={icon}
            title={title}
            data={businessRole}
            editable={i === 0}
            main={i === 0}
            opened={i === 0 && opened}
            setOpened={setOpened}
            editing={editing}
            setEditing={setEditing}
            formOptions={formOptions}
            canEdit={showChangeNP}
            mode={mode}
          />
        );
      })}
    </div>
  );
});

export default BusinessRole;
