import { inject, observer } from 'mobx-react';
import React, { PureComponent } from 'react';
import { GraphStoreType } from '../../../../../stores/graph/graphStore';
import { formatDate } from '../../../../../utils/ui/formatDate';
import { QUARTERS_LABELS } from '../../../../constants';
import Criteria from './Criteria';
import styles from './TypologyParticipant.module.scss';

type TypologyParticipantProps = {
  graphStore?: GraphStoreType;
  nodeData: any;
};

type TypologyParticipantState = {
  nodeData: any;
};

/**
 * Компонент Отображение данных узла схемы Типологии
 */
@inject('graphStore')
@observer
class TypologyParticipant extends PureComponent<
  TypologyParticipantProps,
  TypologyParticipantState
> {
  /**
   * Метод сохраняет данные о НП в состояние компонента
   */
  constructor(props: TypologyParticipantProps) {
    super(props);

    const { nodeData } = props;

    this.state = {
      nodeData,
    };
  }

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const {
      props: { nodeData },
    } = this;

    const getQuarter = (d: any) => {
      return Math.floor((Number(d.slice(5, 7)) + 2) / 3);
    };

    const currentDate = nodeData?.declaration?.period;
    const currentYear = currentDate ? Number(currentDate.slice(0, 4)) : null;

    const currentQuarter = currentDate ? getQuarter(currentDate) : null;
    const defaultQuarterIndex = currentQuarter ? currentQuarter - 1 : null;
    const defaultValue =
      defaultQuarterIndex || defaultQuarterIndex === 0
        ? `${QUARTERS_LABELS[defaultQuarterIndex]} ${currentYear}`
        : null;

    return (
      <div className={styles.wrapper}>
        <div className={styles.containerMainInfo}>
          <div className={styles.dataHeader}>
            <span className={styles.headerLabel}>Общие сведения о налогоплательщике на схеме</span>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Наименование участника схемы:</span>
              <span className={styles.option}>{nodeData.shortName ?? '-'}</span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Наименование аналитической роли:</span>
              <span className={styles.option}>{nodeData.analytical_role_name ?? '-'}</span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>ИНН участника схемы:</span>
              <span className={styles.option}>{nodeData.inn ?? '-'}</span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>КПП участника схемы:</span>
              <span className={styles.option}>{nodeData.kpp ?? '-'}</span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Тип декларации:</span>
              <span className={styles.option}>{nodeData.declaration?.type ?? '-'}</span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Номер налоговой декларации:</span>
              <span className={styles.option}>{nodeData.declaration?.number ?? '-'}</span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Дата подачи декларации:</span>
              <span className={styles.option}>
                {nodeData.declaration?.submissionDate
                  ? formatDate(nodeData.declaration?.submissionDate)
                  : '-'}
              </span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Период налоговой декларации:</span>
              <span className={styles.option}>{defaultValue || '-'}</span>
            </div>
          </div>
        </div>

        {nodeData.criterias && (
          <div className={styles.containerMainInfo}>
            <div className={styles.dataHeader}>
              <span className={styles.headerLabel}>
                Критерии налогоплательщика в схеме (
                {nodeData.criterias.filter((criteria: any) => criteria.value).length})
              </span>
            </div>
            {nodeData.criterias?.map(
              (criteria: any) => criteria.value && <Criteria criteria={criteria} />,
            )}
          </div>
        )}
      </div>
    );
  }
}

export default TypologyParticipant;
