import React, { PureComponent } from 'react';
import styles from './Traceability.module.scss';

import { GraphStoreType } from '../../../../../stores/graph/graphStore';
import { inject, observer } from 'mobx-react';
import Risk from './Risk';

type TraceabilityProps = {
  graphStore?: GraphStoreType;
  nodeData: any;
};

type TraceabilityState = {
  nodeData: any;
};

/**
 * Компонент Риски ПП Прослеживаемость
 */
@inject('graphStore')
@observer
class Traceability extends PureComponent<TraceabilityProps, TraceabilityState> {
  /**
   * Метод сохраняет данные о НП в состояние компонента
   */
  constructor(props: TraceabilityProps) {
    super(props);

    const { nodeData } = props;

    this.state = {
      nodeData,
    };
  }

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const {
      props: { nodeData },
    } = this;

    return (
      <div className={styles.wrapper}>
        <div className={styles.dataHeader}>
          <span className={styles.headerLabel}>Риски ПП Прослеживаемость</span>
        </div>
        <div className={styles.headers}>
          <span></span>
          <span>Наименование индикатора риска</span>
          <span>Описание индикатора риска</span>
          <span>Сумма потенциального ущерба, ₽</span>
        </div>
        {nodeData?.trace_risks?.map((risk: any, index: number) => <Risk risk={risk} />)}
      </div>
    );
  }
}

export default Traceability;
