import { useStores } from 'hooks';
import { observer } from 'mobx-react';
import React, { FormEvent, MouseEvent, useEffect, useState } from 'react';
import { IRole, IRoleFormProps } from './RoleForm.props';

import Dropdown from '../../../../Dropdown';
import Input from '../../../../Input';
import Textarea from '../../../../Textarea';

import { Mode } from 'constants/mode';
import Button from '../../../../Button/Button';
import FileUploader from '../../../../FileUploader/FileUploader';
import styles from './RoleForm.module.scss';

const RoleForm = observer(
  ({
    className,
    data = {
      name: '',
      source: '',
      reason: '',
      comment: '',
      files: [],
    },
    disabled,
    options,
    mode,
    onSubmitRoles,
    ...props
  }: IRoleFormProps) => {
    const {
      graphStore: { state },
    } = useStores();
    const [values, setValues] = useState<IRole>(data);
    const [hasChanges, setHasChanges] = useState<boolean>(false);

    const handleSubmit = (e: FormEvent) => {
      e.preventDefault();
      options.onSave(values);
      onSubmitRoles?.(state !== 'error');
    };

    const handleReset = (e: MouseEvent) => {
      e.preventDefault();
      setValues({ ...data });
    };

    useEffect(() => {
      const prepareData = (data: IRole) =>
        JSON.stringify({
          ...data,
          files: data.files.map(({ file_name }) => file_name),
        });

      setHasChanges(prepareData(values) !== prepareData(data));
    }, [values, data, state]);

    const { saveButtonText = 'Сохранить' } = options;

    return (
      <div className={styles.form} {...props}>
        <form onSubmit={handleSubmit}>
          <div className={`${styles.row} ${styles.isDropdown}`}>
            <div className={styles.col}>
              <Dropdown
                items={options.roleList}
                onSelect={(value) => setValues({ ...values, name: value })}
                value={values.name}
                label={options.roleLabel}
                placeholder={options.rolePlaceHolder}
                disabled={disabled || options.roleDisabled}
                toolTipID={'roleToolTipAnalitic'}
                isToolTip={options.roleDisabled}
                toolTipText={
                  options.roleDisabled
                    ? 'Редактирование в существующей схеме Аналитической роли у корневого узла недоступно'
                    : ''
                }
              />
            </div>
          </div>

          <div className={styles.row}>
            <div className={styles.col}>
              <Input
                label="Источник информации"
                labelPosition="top"
                placeholder="Укажите источник информации"
                onChange={({ target: { value } }) => setValues({ ...values, source: value })}
                value={values.source}
                disabled={disabled}
              />
            </div>

            <div className={styles.col}>
              <Dropdown
                items={options.reasonList}
                onSelect={(value) => setValues({ ...values, reason: value })}
                value={values.reason}
                label={'Причина изменения'}
                placeholder="Выберете из списка"
                disabled={disabled || options.reasonDisabled}
              />
            </div>
          </div>

          <div className={styles.row}>
            <div className={styles.col}>
              <Textarea
                label="Комментарий"
                labelPosition="top"
                placeholder="Ваш комментарий"
                onChange={({ target: { value } }) => setValues({ ...values, comment: value })}
                value={values.comment}
                disabled={disabled}
              />
            </div>
          </div>

          <div className={styles.files}>
            <FileUploader
              fileList={values.files}
              onFilesChange={(files) => setValues({ ...values, files: [...files] })}
              disabled={disabled}
            />
          </div>

          {!disabled && (
            <div className={styles.buttons}>
              <Button
                type="submit"
                disabled={!hasChanges || !values.name || (mode === Mode.EDIT && !values.reason)}
              >
                {saveButtonText}
              </Button>

              <Button mode="transparent" disabled={!hasChanges} onClick={handleReset}>
                Сбросить
              </Button>
            </div>
          )}
        </form>
      </div>
    );
  },
);

export default RoleForm;
