import { StatusesIcons } from 'components/Icon';
import { inject, observer } from 'mobx-react';
import React, { PureComponent } from 'react';
import { Tab, TabList, TabPanel, Tabs } from 'react-tabs';
import { CommonStoreType } from '../../../stores/common/commonStore';
import { GraphStoreType } from '../../../stores/graph/graphStore';
import { getDetailedNodeData } from '../../../utils/services/api/graph';
import Button from '../../Button/Button';
import Loader from '../../Loader';
import { Modal, ModalBody, ModalFooter, ModalHeader } from '../Modal';
import styles from './DetailedNode.module.scss';
import AdditionalInformation from './components/AdditionalInformation';
import AnalyticalRole from './components/AnalyticalRole';
import BasicInformation from './components/BasicInformation';
import BusinessRole from './components/BusinessRole';
import ChangeHistory from './components/ChangeHistory';
import ITAISParticipant from './components/ITAISParticipant';
import Traceability from './components/Traceability/Traceability';
import TypologyParticipant from './components/TypologyParticipant/TypologyParticipant';
import detailedNodeStore from './detailedNodeStore';

type DetailedNodeProps = {
  [keyNode: string]: any;
  graphStore?: GraphStoreType;
  commonStore?: CommonStoreType;
};

type DetailedNodeState = {
  nodeData: any;
  additionalInfoData: any;
  pending: boolean;
  orig_id: string;
};

/**
 * Компонент детализации узла
 */
@inject('graphStore', 'commonStore')
@observer
class DetailedNode extends PureComponent<DetailedNodeProps, DetailedNodeState> {
  constructor(props: DetailedNodeProps) {
    super(props);

    this.state = {
      nodeData: {},
      orig_id: props.is_from_group
        ? props.orig_id
        : props.graphStore?.graphTransformMode === 'ModeV3' &&
          props.nodeData.indicators_bottom?.some((item: any) => item.name === 'Zombie')
        ? props.keyZombie
        : props.nodeData.orig_id,
      pending: true,
      additionalInfoData: {
        founders: [
          {
            p_38: '',
            p_39: '',
            p_40: '',
            p_41: '',
          },
        ],
      },
    };
  }

  _setRolesData = (nodeData: any) => {
    const { commonStore } = this.props;

    detailedNodeStore.setBusinessRole({
      name: nodeData.biz_role_name || '',
      source: nodeData.biz_role_info_source || '',
      reason: commonStore?.getChangeReasonById(nodeData.biz_role_change_reason)?.name || '',
      comment: nodeData.biz_role_description || '',
      files: nodeData.attach_docs_biz_role || [],
    });

    detailedNodeStore.setAnaliticalRole({
      name: nodeData.analytical_role_name || '',
      source: nodeData.role_info_source || '',
      reason: commonStore?.getChangeReasonById(nodeData.role_change_reason)?.name || '',
      comment: nodeData.analytical_role_description || '',
      files: nodeData.attach_docs_role || [],
    });
  };

  /**
   * Метод запрашивает детальную информацию о узле
   */
  async componentDidMount() {
    const { graphStore } = this.props;
    const { orig_id } = this.state;
    const graph_id = graphStore?.previouslyFetchedGraphId || '';

    try {
      const nodeData: any = await getDetailedNodeData(graph_id, orig_id);

      this._setRolesData(nodeData);
      this.setState({ nodeData, pending: false });
    } catch (err) {
      console.warn('Error on fetch node details: ', err.message);
    } finally {
      this.setState({ pending: false });
    }
  }

  _changeRoles = async (informationRole: object) => {
    const { graphStore, commonStore, groupID, params, is_from_group } = this.props;
    const { orig_id } = this.state;
    let new_node_id = null;

    try {
      new_node_id = await graphStore!.changeRoles(informationRole, orig_id);
      // Если переход в НП через детализацию группы (для обновления информации в группе при закрытии НП)
      if (is_from_group) {
        graphStore!.fetchDetailedGroup(groupID, params);
      }
    } catch (err) {
      graphStore!.onError(err);
    } finally {
      this.setState({ pending: false });
    }

    try {
      if (new_node_id) {
        const graph_id = graphStore!.previouslyFetchedGraphId || '';
        const nodeData: any = await getDetailedNodeData(graph_id, new_node_id || '');

        this._setRolesData(nodeData);
        this.setState({ nodeData, pending: false, orig_id: new_node_id });
      }
    } catch (err) {
      graphStore!.onError(err);
      console.warn('Error on fetch node details: ', err.message);
    } finally {
      this.setState({ pending: false });
    }
  };

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { onClose, commonStore, graphStore, isAnalytic } = this.props;
    const { nodeData } = this.state;

    const disabled = true;
    const disabledRoles = false;

    const {
      node_change_statuses,
      change_histories,
      founders,
      trace_risks,
      typology_participant,
      itais_participant,
    } = nodeData;

    return (
      <Modal>
        <ModalHeader onClose={onClose}>
          <div className={styles.headerLabel}>
            <span data-draggable>Детализация налогоплательщика</span>
            <StatusesIcons statuses={[node_change_statuses?.change_status] || []} />
          </div>
        </ModalHeader>

        <ModalBody withFooter={true}>
          <div className={styles.wrapper}>
            <Tabs
              className={styles.tabs}
              defaultIndex={isAnalytic ? 2 : graphStore?.sourceSystem === 'ITAIS' ? 7 : 0}
              selectedIndex={graphStore?.sourceSystem === 'ITAIS' ? 0 : undefined}
            >
              <TabList className={styles.tabList}>
                {graphStore?.sourceSystem !== 'ITAIS' && (
                  <Tab className={styles.tabItem} title={'Сведения о НП и его сделках'}>
                    Общая информация
                  </Tab>
                )}
                {graphStore?.showBusiness && graphStore?.sourceSystem !== 'ITAIS' && (
                  <Tab className={styles.tabItem} title={'Сведения о присвоенной НП Бизнес роли'}>
                    <span>Бизнес роль</span>
                    <StatusesIcons
                      statuses={[node_change_statuses?.biz_role_change_status] || []}
                    />
                  </Tab>
                )}
                {graphStore?.showAnalytic && graphStore?.sourceSystem !== 'ITAIS' && (
                  <Tab
                    className={styles.tabItem}
                    title={'Сведения о присвоенной НП Аналитической роли в схеме'}
                  >
                    <span>Аналитическая роль</span>
                    <StatusesIcons statuses={[node_change_statuses?.role_change_status] || []} />
                  </Tab>
                )}
                {trace_risks?.length > 0 && graphStore?.sourceSystem !== 'ITAIS' && (
                  <Tab className={styles.tabItem} title={'Отображение рисков Прослеживаемости'}>
                    Прослеживаемость
                  </Tab>
                )}
                {typology_participant && graphStore?.sourceSystem !== 'ITAIS' && (
                  <Tab className={styles.tabItem} title={'Отображение данных узла схемы Типологии'}>
                    Схема
                  </Tab>
                )}
                {founders?.length > 0 && graphStore?.sourceSystem !== 'ITAIS' && (
                  <Tab className={styles.tabItem} title={'Сведения об учредителях НП'}>
                    Доп. информация
                  </Tab>
                )}
                {change_histories?.length > 0 && graphStore?.sourceSystem !== 'ITAIS' && (
                  <Tab className={styles.tabItem} title={'История изменений НП'}>
                    История
                  </Tab>
                )}
                {itais_participant && graphStore?.sourceSystem === 'ITAIS' && (
                  <Tab className={styles.tabItem} title={'Отображение данных узла схемы'}>
                    Схема
                  </Tab>
                )}
              </TabList>
              {graphStore?.sourceSystem !== 'ITAIS' && (
                <TabPanel className={styles.tabPanel}>
                  {Object.keys(nodeData).length > 0 ? (
                    <BasicInformation
                      disabled={disabled}
                      nodeData={nodeData}
                      theme={commonStore?.appTheme}
                    />
                  ) : (
                    <div className={styles.loader}>
                      <Loader />
                    </div>
                  )}
                </TabPanel>
              )}
              {graphStore?.showBusiness && graphStore?.sourceSystem !== 'ITAIS' && (
                <TabPanel className={styles.tabPanel}>
                  {Object.keys(nodeData).length > 0 ? (
                    <BusinessRole
                      disabled={disabledRoles}
                      nodeData={nodeData}
                      changeRole={this._changeRoles}
                    />
                  ) : (
                    <div className={styles.loader}>
                      <Loader />
                    </div>
                  )}
                </TabPanel>
              )}
              {graphStore?.showAnalytic && graphStore?.sourceSystem !== 'ITAIS' && (
                <TabPanel className={styles.tabPanel}>
                  {Object.keys(nodeData).length > 0 ? (
                    <AnalyticalRole
                      disabled={disabledRoles}
                      nodeData={nodeData}
                      changeRole={this._changeRoles}
                    />
                  ) : (
                    <div className={styles.loader}>
                      <Loader />
                    </div>
                  )}
                </TabPanel>
              )}
              {trace_risks?.length > 0 && graphStore?.sourceSystem !== 'ITAIS' && (
                <TabPanel className={styles.tabPanel}>
                  {Object.keys(nodeData).length > 0 ? (
                    <Traceability nodeData={nodeData} />
                  ) : (
                    <div className={styles.loader}>
                      <Loader />
                    </div>
                  )}
                </TabPanel>
              )}
              {typology_participant && graphStore?.sourceSystem !== 'ITAIS' && (
                <TabPanel className={styles.tabPanel}>
                  {Object.keys(nodeData).length > 0 ? (
                    <TypologyParticipant nodeData={nodeData.typology_participant} />
                  ) : (
                    <div className={styles.loader}>
                      <Loader />
                    </div>
                  )}
                </TabPanel>
              )}
              {founders?.length > 0 && graphStore?.sourceSystem !== 'ITAIS' && (
                <TabPanel className={styles.tabPanel}>
                  {Object.keys(nodeData).length > 0 ? (
                    <AdditionalInformation disabled={disabled} nodeData={nodeData} />
                  ) : (
                    <div className={styles.loader}>
                      <Loader />
                    </div>
                  )}
                </TabPanel>
              )}
              {change_histories?.length > 0 && graphStore?.sourceSystem !== 'ITAIS' && (
                <TabPanel className={styles.tabPanel}>
                  {Object.keys(nodeData).length > 0 ? (
                    <ChangeHistory data={nodeData.change_histories} />
                  ) : (
                    <div className={styles.loader}>
                      <Loader />
                    </div>
                  )}
                </TabPanel>
              )}
              {itais_participant && graphStore?.sourceSystem === 'ITAIS' && (
                <TabPanel className={styles.tabPanel}>
                  {Object.keys(nodeData).length > 0 ? (
                    <ITAISParticipant
                      nodeData={nodeData.itais_participant}
                      nodeDataMain={nodeData}
                      disabled={true}
                      theme={commonStore?.appTheme}
                    />
                  ) : (
                    <div className={styles.loader}>
                      <Loader />
                    </div>
                  )}
                </TabPanel>
              )}
            </Tabs>
          </div>
        </ModalBody>

        <ModalFooter>
          <div className={styles.bottomBar}>
            <Button mode="secondary" onClick={onClose}>
              Закрыть
            </Button>
          </div>
        </ModalFooter>
      </Modal>
    );
  }
}

export default DetailedNode;
