import { Mode } from 'constants/mode';
import { observer } from 'mobx-react';
import React, { useEffect, useState } from 'react';
import { useStores } from '../../../../../hooks';
import RoleForm from '../RoleForm';
import { IRole } from '../RoleForm/RoleForm.props';
import RolesHeader from '../RolesHeader';
import RolesItem from '../RolesItem';
import styles from './AnalyticalRole.module.scss';

interface IBusinessRoleProps {
  disabled?: boolean;
  nodeData: any;
  changeRole: (information: object) => void;
}

const AnalyticalRole = observer(({ disabled, nodeData, changeRole }: IBusinessRoleProps) => {
  const {
    commonStore: {
      analyticalRolesIcons,
      getAnalyticalRoleById,
      getAnalyticalRoleByName,
      getChangeReasonByName,
      analyticalRolesList,
      changeReasonsList,
    },
    detailedNodeStore: { analiticalRole },
    graphStore: { controlArea, showChangeNP, showReasons },
  } = useStores();

  const [opened, setOpened] = useState<boolean>(false);
  const [editing, setEditing] = useState<boolean>(opened);

  const onSave = (data: IRole) => {
    changeRole({
      role_name: data.name,
      role_id: getAnalyticalRoleByName(data.name)?.role_id || null,
      role_description: data.comment,
      role_change_reason: getChangeReasonByName(data.reason)?.reason_id || null,
      role_info_source: data.source,
      attach_docs_role: data.files,
    });
  };

  const hasControlArea = (el: any) => el.control_areas.some((item: any) => item === controlArea);

  const formOptions = {
    roleList: analyticalRolesList
      .filter((el: any) => el.used_in_vsa && hasControlArea(el))
      .map(({ value }: { value: string }) => value),
    reasonList: changeReasonsList.map(({ name }: { name: string }) => name),
    roleLabel: 'Наименование аналитической роли',
    showRoleLabel: 'Просмотр аналитической роли',
    rolePlaceHolder: 'Укажите наименование аналитической роли',
    roleDisabled: nodeData.is_vgp_denied_access,
    reasonDisabled: !showReasons,
    onSave,
  };

  const hasRoles = !!analiticalRole.name;
  const [mode, setMode] = useState(Mode.CREATE);

  useEffect(() => setMode(hasRoles ? Mode.EDIT : Mode.CREATE), [hasRoles]);

  return (
    <div className={styles.container}>
      <RolesHeader
        title="Аналитическая роль налогоплательщика"
        roleName="аналитическую роль"
        hasRoles={hasRoles}
        setOpened={() => {
          setOpened((e) => !e);
          setEditing(true);
        }}
        canEdit={showChangeNP}
      >
        <RoleForm options={{ ...formOptions, saveButtonText: 'Добавить' }} mode={mode} />
      </RolesHeader>
      {nodeData?.role_id && (
        <RolesItem
          icon={
            analyticalRolesIcons[getAnalyticalRoleById(nodeData.role_id)?.brief || '']?.icon || ''
          }
          title={analiticalRole.name}
          data={analiticalRole}
          editable={true}
          canEdit={showChangeNP}
          opened={opened}
          setOpened={setOpened}
          editing={editing}
          setEditing={setEditing}
          formOptions={formOptions}
          mode={mode}
        />
      )}
    </div>
  );
});

export default AnalyticalRole;
