import React, { PureComponent } from 'react';
import styles from './ITAISParticipant.module.scss';

import { GraphStoreType } from '../../../../../stores/graph/graphStore';
import { inject, observer } from 'mobx-react';
import InputWithLabel from '../../../../InputWithLabel';

import { QUARTERS_LABELS } from '../../../../constants';

import { formatDate } from '../../../../../utils/ui/formatDate';
import { formatMoneyDetailedNode } from '../../../../../utils/ui/formatMoneyDetailedNode';

type ITAISParticipantProps = {
  graphStore?: GraphStoreType;
  nodeData: any;
  nodeDataMain: any;
  disabled: boolean;
  theme: any;
};

type ITAISParticipantState = {
  nodeData: any;
};

/**
 * Компонент Отображение данных узла схемы ИСА КН
 */
@inject('graphStore')
@observer
class ITAISParticipant extends PureComponent<ITAISParticipantProps, ITAISParticipantState> {
  constructor(props: ITAISParticipantProps) {
    super(props);

    const { nodeData } = props;

    this.state = {
      nodeData,
    };
  }

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const {
      props: { nodeData, disabled, nodeDataMain },
    } = this;

    const getQuarter = (d: any) => {
      return Math.floor((Number(d.slice(5, 7)) + 2) / 3);
    };

    const type_participant =
      nodeData?.participant_type === 'RU' ? 'ru_participant' : 'by_participant';

    let defaultValue;
    if (nodeData[type_participant]?.declaration) {
      const currentDate = nodeData[type_participant]?.declaration?.period;
      const currentYear = Number(currentDate.slice(0, 4));
      const currentQuarter = getQuarter(currentDate);
      const defaultQuarterIndex = currentQuarter - 1;
      defaultValue = `${QUARTERS_LABELS[defaultQuarterIndex]} ${currentYear}`;
    } else {
      defaultValue = null;
    }

    return (
      <div className={styles.wrapper}>
        <div className={styles.container}>
          <InputWithLabel
            disabled={disabled}
            className={styles.containerLarge}
            value={nodeDataMain.short_name ?? 'нет данных'}
            onChange={() => {}}
            label="Сокращенное наименование НП"
            onCopy={true}
          />

          <InputWithLabel
            disabled={disabled}
            className={styles.containerMinor}
            value={nodeDataMain.inn ?? 'нет данных'}
            onChange={() => {}}
            label="ИНН НП"
            onCopy={true}
          />
        </div>
        <div className={styles.container}>
          {type_participant === 'ru_participant' && (
            <>
              <InputWithLabel
                disabled={disabled}
                className={styles.containerLarge}
                value={nodeData.director_name ? nodeData.director_name : 'нет данных'}
                onChange={() => {}}
                label="Руководитель"
              />
              <InputWithLabel
                disabled={disabled}
                className={styles.containerMinor}
                value={nodeData.director_inn ? nodeData.director_inn : 'нет данных'}
                onChange={() => {}}
                label="ИНН"
              />
            </>
          )}
        </div>
        <div className={styles.container}>
          <InputWithLabel
            disabled={disabled}
            className={styles.containerLarge}
            value={nodeData.okved ? nodeData.okved : 'нет данных'}
            onChange={() => {}}
            label="Наименование ОКВЭД"
            isToolTip
            toolTipText={nodeData.okved ? nodeData.okved : 'нет данных'}
            toolTipID={'okved'}
          />

          <InputWithLabel
            disabled={disabled}
            className={styles.containerMinor}
            value={nodeData.okved_code ? nodeData.okved_code : 'нет данных'}
            onChange={() => {}}
            label="Код ОКВЭД"
          />
        </div>
        <div className={styles.container}>
          <InputWithLabel
            disabled={disabled}
            className={styles.containerLarge}
            value={nodeData.seller_count ? nodeData.seller_count : 'нет данных'}
            onChange={() => {}}
            label="Количество продавцов"
          />

          <InputWithLabel
            disabled={disabled}
            className={styles.containerMinor}
            value={nodeData.buyers_count ? nodeData.buyers_count : 'нет данных'}
            onChange={() => {}}
            label="Количество покупателей"
          />
        </div>

        {nodeData[type_participant]?.declaration && (
          <>
            <div className={styles.container}>
              <InputWithLabel
                disabled={disabled}
                className={styles.containerLarge}
                value={
                  nodeData[type_participant]?.declaration?.reg_number
                    ? nodeData[type_participant]?.declaration?.reg_number
                    : 'нет данных'
                }
                onChange={() => {}}
                label="Номер налоговой декларации"
              />
              <InputWithLabel
                disabled={disabled}
                className={styles.containerMinor}
                value={
                  nodeData[type_participant]?.declaration?.vat_amount
                    ? formatMoneyDetailedNode(nodeData[type_participant]?.declaration?.vat_amount)
                    : 'нет данных'
                }
                onChange={() => {}}
                label={
                  'Сумма по налоговой декларации ' +
                  (type_participant === 'ru_participant' ? '₽' : 'Br')
                }
              />
            </div>

            <div className={styles.container}>
              <InputWithLabel
                disabled={disabled}
                className={styles.containerLarge}
                value={
                  nodeData[type_participant]?.declaration?.submission_date
                    ? formatDate(nodeData[type_participant]?.declaration?.submission_date)
                    : 'нет данных'
                }
                onChange={() => {}}
                label="Дата подачи декларации"
              />

              <InputWithLabel
                disabled={disabled}
                className={styles.containerMinor}
                value={defaultValue ? defaultValue : 'нет данных'}
                onChange={() => {}}
                label="Период налоговой декларации"
              />
            </div>
          </>
        )}
      </div>
    );
  }
}

export default ITAISParticipant;
