import Button from 'components/Button';
import React, { HTMLAttributes, ReactNode } from 'react';
import DropdownButton from '../../../../DropdownButton';
import styles from './RolesHeader.module.scss';

interface IRolesHeaderProps extends HTMLAttributes<HTMLDivElement> {
  children?: ReactNode;
  roleName?: string;
  hasRoles?: boolean;
  count?: number;
  setOpened?: () => void;
  canEdit?: boolean;
}

const RolesHeader = ({
  className,
  children,
  count,
  hasRoles,
  roleName,
  title,
  setOpened,
  canEdit,
  ...props
}: IRolesHeaderProps) => {
  return (
    <div className={styles.header} {...props}>
      <div className={styles.title}>
        {title}
        {(count || count === 0) && <span className={styles.count}>{count}</span>}
      </div>

      {canEdit &&
        (hasRoles ? (
          <Button
            mode="secondary"
            size="small"
            onClick={setOpened}
          >{`Редактировать ${roleName}`}</Button>
        ) : (
          <DropdownButton label={`Добавить ${roleName}`} mode="secondary" size="small">
            <div className={styles.form}>{children}</div>
          </DropdownButton>
        ))}
    </div>
  );
};

export default RolesHeader;
