import React, { PureComponent, BaseSyntheticEvent } from 'react';
import styles from './AdditionalInformation.module.scss';

import Accordion from '../../../../Accordion';
import ActionButton from '../../../../ActionButton';

import { formatMoneyDetailedNode } from '../../../../../utils/ui/formatMoneyDetailedNode';
import { formatDate } from '../../../../../utils/ui/formatDate';

type AdditionalInformationProps = {
  nodeData: any;
  disabled?: boolean;
};

type AdditionalInformationState = {
  nodeData: any;
};

const CustomDiv = (props: any) => {
  return <div>{props.children}</div>;
};

/**
 * Компонент окна дополнитекльной информации в детализации НП.
 */
class AdditionalInformation extends PureComponent<
  AdditionalInformationProps,
  AdditionalInformationState
> {
  /**
   * Метод инициализирует учредителей
   *
   * @param {AdditionalInformationProps} props - свойства компонента
   */
  constructor(props: AdditionalInformationProps) {
    super(props);

    const { nodeData } = props;

    this.state = {
      nodeData,
    };
  }

  /**
   * Метод изменяет ИНН в данных учредителя
   *
   * @param {number} founderIndexToChange - индекс учредителя для изменения
   * @param {string} text - новый текст
   */
  _onINNChange = (founderIndexToChange: number, event: BaseSyntheticEvent) => {};

  /**
   * Метод изменяет название в данных учредителя
   *
   * @param {number} founderIndexToChange - индекс учредителя для изменения
   * @param {string} text - новый текст
   */
  _onNameChange = (founderIndexToChange: number, event: BaseSyntheticEvent) => {};

  /**
   * Метод изменяет уставной капитал в данных учредителя
   *
   * @param {number} founderIndexToChange - индекс учредителя для изменения
   * @param {string} text - новый текст
   */
  _onAuthorizedCapitalChange = (founderIndexToChange: number, event: BaseSyntheticEvent) => {};

  /**
   * Метод изменяет долю в данных учредителя
   *
   * @param {number} founderIndexToChange - индекс учредителя для изменения
   * @param {string} text - новый текст
   */
  _onShareChange = (founderIndexToChange: number, event: BaseSyntheticEvent) => {};

  /**
   * Метод вызывается при сохранении
   */
  _onSave = (): void => {};

  /**
   * Метод вызывается при отмене
   */
  _onClose = (): void => {};

  /**
   * Метод отрисовывает элементы
   */
  render() {
    const {
      // state: { nodeData },
      props: { disabled, nodeData },
    } = this;

    return (
      <div className={styles.wrapper}>
        <div className={styles.dataHeader}>
          <span className={styles.headerLabel}>Дополнительная информация</span>
        </div>
        {nodeData?.founders?.length ? (
          <Accordion allowMultipleOpen>
            {nodeData?.founders?.map((founder: any, index: number) => (
              <CustomDiv
                key={'additional-info-founders-item-' + index}
                label={'Сведения об учредителе ' + (index + 1)}
                isOpen={index === 0}
                isArrowFront
                labelContainerClassName={styles.labelContainer}
              >
                <div className={styles.containerMainInfo}>
                  <div className={styles.mainInfoItem}>
                    <div className={styles.col1}>
                      <span className={styles.title}>ИНН учредителя:</span>
                      <span className={styles.option}>{founder.inn ?? '-'}</span>
                    </div>

                    <div className={styles.col1}>
                      <span className={styles.title}>Наименование учредителя:</span>
                      <span className={styles.option}>{founder.name ?? '-'}</span>
                    </div>
                  </div>

                  <div className={styles.mainInfoItem}>
                    <div className={styles.col1}>
                      <span className={styles.title}>Уставной капитал, ₽:</span>
                      <span className={styles.option}>
                        {founder.capital_value
                          ? formatMoneyDetailedNode(founder.capital_value)
                          : '-'}
                      </span>
                    </div>

                    <div className={styles.col1}>
                      <span className={styles.title}>Доля в уставном капитале, %:</span>
                      <span className={styles.option}>{founder.capital_percent ?? '-'}</span>
                    </div>
                  </div>

                  <div className={styles.mainInfoItem}>
                    <div className={styles.col1}>
                      <span className={styles.title}>Дата начала учредительства:</span>
                      <span className={styles.option}>
                        {founder.date_start ? formatDate(founder.date_start) : '-'}
                      </span>
                    </div>

                    <div className={styles.col1}>
                      <span className={styles.title}>Дата окончания учредительства:</span>
                      <span className={styles.option}>
                        {founder.date_end ? formatDate(founder.date_end) : '-'}
                      </span>
                    </div>
                  </div>
                </div>
              </CustomDiv>
            ))}
          </Accordion>
        ) : (
          ''
        )}

        {!disabled && (
          <div className={styles.actions}>
            <div className={styles.action}>
              <ActionButton onClick={this._onSave} label="Сохранить" />
            </div>

            <div className={styles.action}>
              <ActionButton isSecondary onClick={this._onClose} label="Отменить" />
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default AdditionalInformation;
