import { inject, observer } from 'mobx-react';
import React, { PureComponent } from 'react';
import { GraphStoreType } from '../../../../../../stores/graph/graphStore';
import Icon from '../../../../../Icon';
import Parameters from '../Parameters';
import styles from './Criteria.module.scss';
import { ReactComponent as NoParamIcon } from './assets/noParam.svg';
import { ReactComponent as YesParamIcon } from './assets/yesParam.svg';

type CriteriaProps = {
  graphStore?: GraphStoreType;
  criteria: any;
};

type CriteriaState = {
  isOpen: boolean;
};

/**
 * Компонент Критерии
 */
@inject('graphStore')
@observer
class Criteria extends PureComponent<CriteriaProps, CriteriaState> {
  constructor(props: CriteriaProps) {
    super(props);

    this.state = {
      isOpen: false,
    };
  }

  openCriteria = () => {
    const { isOpen } = this.state;

    this.setState({ isOpen: !isOpen });
  };

  render() {
    const {
      props: { criteria },
      state: { isOpen },
    } = this;

    return (
      <div className={styles.wrapper}>
        <div className={styles.criteria} onClick={this.openCriteria}>
          <div>
            <Icon className={styles.arrowFront} name={isOpen ? 'arrow_up' : 'arrow_down'} />
            {criteria.name}
          </div>
          {criteria.value ? <YesParamIcon /> : <NoParamIcon />}
        </div>
        {isOpen &&
          criteria.parameters &&
          criteria.parameters?.map((parameter: any) => <Parameters parameter={parameter} />)}
      </div>
    );
  }
}

export default Criteria;
