import React, { PureComponent } from 'react';
import styles from './BasicInformation.module.scss';

import DropdownButton from 'components/DropdownButton/DropdownButton';
import TransferListItem from '../TransferListItem';
import { PanelGroup, Panel } from '../../../../PanelGroup';
import { formatDate } from '../../../../../utils/ui/formatDate';

import { formatIntegralAssessment } from '../../../../../utils/ui/formatIntegralAssessment';
import { GraphStoreType } from '../../../../../stores/graph/graphStore';
import { inject, observer } from 'mobx-react';
import { formatMoneyDetailedNode } from '../../../../../utils/ui/formatMoneyDetailedNode';
import PeriodDataFilter from 'stores/filters/periodDataFilter';
import UnifiedClientFacade from 'utils/services/UnifiedClientFacade';
import FilterStore from 'stores/filters/filterStore';
import { getPeriodNumbers } from 'utils/getPeriodNumbers';

type BasicInformationProps = {
  graphStore?: GraphStoreType;
  periodDataFilter?: PeriodDataFilter;
  filterStore?: FilterStore;
  disabled?: boolean;
  nodeData: any;
  theme?: string;
};

type BasicInformationState = {
  nodeData: any;
};

/**
 * Компонент основной информации узла
 */
@inject('graphStore', 'periodDataFilter', 'filterStore')
@observer
class BasicInformation extends PureComponent<BasicInformationProps, BasicInformationState> {
  /**
   * Метод сохраняет данные о НП в состояние компонента
   */
  constructor(props: BasicInformationProps) {
    super(props);

    const { nodeData } = props;

    this.state = {
      nodeData,
    };
  }

  /**
   * Функция перехода в стороннюю подсистему
   * @private
   */
  _onTransfer = async (
    functionName: string,
    context: string | Record<string, string | number>,
    errorMessage: string,
    browserLink?: string,
  ) => {
    const { graphStore } = this.props;
    graphStore?.statePending();

    try {
      await UnifiedClientFacade.executeFunction('Vsa2Domain', functionName, context, browserLink);
    } catch (e) {
      e.message = errorMessage;
      graphStore?.onError(e);
    } finally {
      graphStore?.stateDone();
    }
  };

  /**
   * Функция получения данных для перехода в карточки НП
   * @private
   */
  _getTransferToTaxpayerData = () => {
    const { filterStore, nodeData } = this.props;
    const { year, quarter } = getPeriodNumbers(filterStore?.options?.nds3_period_to);

    return {
      inn: nodeData.inn,
      name: nodeData.short_name,
      kppEffective: nodeData.kpp_effective,
      year,
      quarter,
    };
  };

  /**
   * Функция "Перейти в ДНП"
   * @private
   */
  _onGoToDnpByFid = async (): Promise<void> => {
    const { nodeData } = this.props;
    const { fid, subject_type } = nodeData;
    let subjectType = subject_type === 'UL_IP' ? 'ul' : subject_type === 'FL' ? 'fl' : '';

    const browserLink = `/inias/csc/gws/static/dnpwweb/` + subjectType + '/' + fid + '/dashboard';

    this._onTransfer(
      'GoToDnpByFid',
      {
        fid: fid,
        subject_type: subject_type,
      },
      'Невозможно перейти в в досье НП',
      browserLink,
    );
  };

  /**
   * Функция "Перейти в НД НДС"
   * @private
   */
  _onGoToDeclarationByTaxPayer = () => {
    this._onTransfer(
      'GoToDeclarationByTaxPayer',
      this._getTransferToTaxpayerData(),
      'Невозможно перейти в НД НДС',
    );
  };

  /**
   * Функция "Перейти в карточку НП в АСК НДС"
   * @private
   */
  _onGoToAskNdsByTaxPayer = () => {
    const { nodeData } = this.props;

    this._onTransfer(
      'GoToAskNdsByTaxPayer',
      {
        inn: nodeData.inn,
        kppEffective: nodeData.kpp_effective,
      },
      'Невозможно перейти в карточку НП в АСК НДС',
    );
  };

  /**
   * Функция "Перейти в Отложенные вычеты покупателя"
   * @private
   */
  _onGoToDeductionsByBuyerCard = () => {
    this._onTransfer(
      'GoToDeductionsByBuyerCard',
      this._getTransferToTaxpayerData(),
      'Невозможно перейти в "Отложенные вычеты покупателя"',
    );
  };

  /**
   * Функция "Перейти в Отложенные вычеты продавца"
   * @private
   */
  _onGoToDeductionsBySellerCard = () => {
    this._onTransfer(
      'GoToDeductionsBySellerCard',
      this._getTransferToTaxpayerData(),
      'Невозможно перейти в "Отложенные вычеты продавца"',
    );
  };

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const {
      props: { disabled, nodeData, theme, periodDataFilter, filterStore, graphStore },
    } = this;
    const avgHeadcountPeriod = periodDataFilter?.options.nds3_period_to?.split(';');
    const transferIsEnabled = nodeData?.kpp_effective && nodeData?.inn;

    return (
      <div className={styles.wrapper}>
        <div className={styles.containerMainInfo}>
          <div className={styles.dataHeader}>
            <span className={styles.headerLabel}>Общая информация о налогоплательщике</span>
            {graphStore?.sourceSystem !== 'ITAIS' && (
              <DropdownButton
                label="Перейти"
                title={'Переход в подсистемы ДНП и АСК НДС 2'}
                mode="secondary"
                size="small"
              >
                <PanelGroup>
                  <Panel header="ДНП" mode="secondary">
                    <TransferListItem
                      label="Перейти в ДНП"
                      transferIsEnabled
                      isEKP
                      title="Переход в подсистему Досье налогоплательщика по выбранному НП"
                      onClick={this._onGoToDnpByFid}
                    />
                  </Panel>

                  <Panel header="АСК НДС" mode="secondary">
                    <TransferListItem
                      transferIsEnabled={transferIsEnabled && typeof window.CefSharp === 'object'}
                      isEKP={typeof window.CefSharp === 'object'}
                      label="Декларация НДС"
                      title="Переход в НД НДС"
                      onClick={this._onGoToDeclarationByTaxPayer}
                    />

                    {filterStore?.options?.is_ask_nds2 && (
                      <>
                        <TransferListItem
                          transferIsEnabled={
                            transferIsEnabled && typeof window.CefSharp === 'object'
                          }
                          isEKP={typeof window.CefSharp === 'object'}
                          label="Карточка налогоплательщика"
                          title="Переход в карточку НП в АСК НДС 2"
                          onClick={this._onGoToAskNdsByTaxPayer}
                        />

                        <TransferListItem
                          transferIsEnabled={
                            transferIsEnabled && typeof window.CefSharp === 'object'
                          }
                          isEKP={typeof window.CefSharp === 'object'}
                          label="Отложенные вычеты покупателя"
                          title={'Переход в "Отложенные вычеты покупателя"'}
                          onClick={this._onGoToDeductionsByBuyerCard}
                        />

                        <TransferListItem
                          transferIsEnabled={
                            transferIsEnabled && typeof window.CefSharp === 'object'
                          }
                          isEKP={typeof window.CefSharp === 'object'}
                          label="Отложенные вычеты продавца"
                          title={'Переход в "Отложенные вычеты продавца"'}
                          onClick={this._onGoToDeductionsBySellerCard}
                        />
                      </>
                    )}
                  </Panel>
                </PanelGroup>
              </DropdownButton>
            )}
          </div>
          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>ИНН:</span>
              <span className={styles.option}>{nodeData.inn ?? '-'}</span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Краткое наименование:</span>
              <span className={styles.option}>{nodeData.short_name ?? '-'}</span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Дата регистрации:</span>
              <span className={styles.option}>
                {nodeData.registration_date ? formatDate(nodeData.registration_date) : '-'}
              </span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Руководитель:</span>
              <span className={styles.option}>{nodeData.director ?? '-'}</span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Статус:</span>
              <span className={styles.option}>{nodeData.status ?? '-'}</span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>СУР АСК НДС-2:</span>
              <span className={styles.option}>
                {nodeData.score ? formatIntegralAssessment(nodeData.score) : '-'}
              </span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Код НО, постановки на учет:</span>
              <span className={styles.option}>{nodeData.ufns_code ?? '-'}</span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Применяемый режим налогообложения:</span>
              <span className={styles.option}>{nodeData.tax_regime ?? '-'}</span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Ср. списочная чис. персонала по РСВ, чел.:</span>
              <span className={styles.option}>{nodeData.avg_headcount ?? '-'}</span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Период выведения информации (РСВ):</span>
              <span className={styles.option}>
                {avgHeadcountPeriod
                  ? `${avgHeadcountPeriod[0]} кв. ${avgHeadcountPeriod[1]} г.`
                  : '-'}
              </span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Ср. списочная чис. персонала по 6-НДФЛ, чел.:</span>
              <span className={styles.option}>{nodeData.avg_headcount_6ndfl ?? '-'}</span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Период выведения информации (6-НДФЛ):</span>
              <span className={styles.option}>
                {avgHeadcountPeriod
                  ? `${avgHeadcountPeriod[0]} кв. ${avgHeadcountPeriod[1]} г.`
                  : '-'}
              </span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Дата ликвидации:</span>
              <span className={styles.option}>
                {nodeData.liquidation_date ? formatDate(nodeData.liquidation_date) : '-'}
              </span>
            </div>
          </div>

          <div className={styles.mainInfoItem}></div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Признак наличия имущества:</span>
              <span className={styles.option}>{nodeData.is_property_available ? 'Да' : 'Нет'}</span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Стоимость имущества, ₽:</span>
              <span className={styles.option}>
                {formatMoneyDetailedNode(nodeData.property_amount)}
              </span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Признак наличия недостоверности:</span>
              <span className={styles.option}>
                {nodeData.falsity?.unreliability_code ? 'Да' : 'Нет'}
              </span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Недостоверность:</span>
              <span className={styles.option}>{nodeData.falsity?.unreliability_name ?? '-'}</span>
            </div>
          </div>
        </div>

        <div className={styles.containerMainInfo}>
          <div className={styles.dataHeader}>
            <span className={styles.dataHeaderLabel}>
              Сводная информация о сделках налогоплательщика
            </span>
          </div>
          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Входящие платежи, ₽:</span>
              <span className={styles.option}>
                {nodeData.pay_debit ? formatMoneyDetailedNode(nodeData.pay_debit) : '-'}
              </span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Исходящие платежи, ₽:</span>
              <span className={styles.option}>
                {nodeData.pay_credit ? formatMoneyDetailedNode(nodeData.pay_credit) : '-'}
              </span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Сумма НДС исчисл., ₽:</span>
              <span className={styles.option}>
                {nodeData.vat_calc ? formatMoneyDetailedNode(nodeData.vat_calc) : '-'}
              </span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Сумма НДС к уплате, ₽:</span>
              <span className={styles.option}>
                {nodeData.vat_pay ? formatMoneyDetailedNode(nodeData.vat_pay) : '-'}
              </span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Сумма НДС к вычету, ₽:</span>
              <span className={styles.option}>
                {nodeData.vat_deduct ? formatMoneyDetailedNode(nodeData.vat_deduct) : '-'}
              </span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>TG:</span>
              <span className={styles.option}>
                {nodeData.tax_gap && nodeData.tax_gap !== '0' ? nodeData.tax_gap + '%' : '-'}
              </span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>Доля "чистых" вычетов по НДС, %:</span>
              <span className={styles.option}>
                {nodeData.deduction_vat_sum && nodeData.deduction_vat_sum.length > 0
                  ? nodeData.deduction_vat_sum[0]?.value
                  : '-'}
              </span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>Доля "схемных" вычетов по НДС, %:</span>
              <span className={styles.option}>
                {nodeData.scheme_deduction_sum && nodeData.deduction_vat_sum.length > 0
                  ? nodeData.scheme_deduction_sum[0]?.value
                  : '-'}
              </span>
            </div>
          </div>

          <div className={styles.mainInfoItem}>
            <div className={styles.col1}>
              <span className={styles.title}>
                НДС по журналу учета выставленных сч/ф продавца, ₽:
              </span>
              <span className={styles.option}>
                {nodeData.vat10total || nodeData.vat10total === 0
                  ? formatMoneyDetailedNode(nodeData.vat10total)
                  : '-'}
              </span>
            </div>

            <div className={styles.col1}>
              <span className={styles.title}>
                НДС по журналу учета полученных сч/ф покупателя, ₽:
              </span>
              <span className={styles.option}>
                {nodeData.vat11total || nodeData.vat11total === 0
                  ? formatMoneyDetailedNode(nodeData.vat11total)
                  : '-'}
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default BasicInformation;
