import React, { Dispatch, HTMLAttributes, ReactNode, SetStateAction } from 'react';
import RoleForm from '../RoleForm/RoleForm';
import { IOptions, IRole } from '../RoleForm/RoleForm.props';
import styles from './RolesItem.module.scss';
import { ReactComponent as EditIcon } from './assets/edit.svg';

interface IRolesItemProps extends HTMLAttributes<HTMLDivElement> {
  children?: ReactNode;
  data: IRole;
  editable?: boolean;
  main?: boolean;
  isAnalytical?: boolean;
  icon?: string;
  formOptions: IOptions;
  opened: boolean;
  setOpened: Dispatch<SetStateAction<boolean>>;
  editing: boolean;
  setEditing: Dispatch<SetStateAction<boolean>>;
  canEdit?: boolean;
  mode?: string;
}

const RolesItem = ({
  className,
  children,
  data,
  title,
  editable,
  main,
  isAnalytical,
  icon,
  formOptions,
  opened,
  setOpened,
  editing,
  setEditing,
  canEdit,
  mode,
  ...props
}: IRolesItemProps) => {
  return (
    <div className={styles.item} {...props}>
      <div
        className={`${styles.header} ${editable && styles.editable}`}
        onClick={() => {
          setOpened((e) => !e);
          setEditing(false);
        }}
      >
        <div className={styles.title}>
          {icon && (
            <span className={styles.icon}>
              <img src={icon} alt={title} />
            </span>
          )}
          {title}
        </div>
        <div className={styles.bar}>
          {main && <span className={styles.main}>Основная бизнес-роль</span>}
          {editable && (
            <div className={`${styles.plus} ${opened && styles.isOpened}`}>
              <div className={`${styles.line} ${styles.line_1}`}></div>
              {!opened && <div className={`${styles.line} ${styles.line_2}`}></div>}
            </div>
          )}
        </div>
      </div>
      {editable && opened && (
        <div className={styles.form}>
          <div className={styles.form__header}>
            <div className={styles.form__title}>{formOptions.showRoleLabel}</div>
            {canEdit && (
              <span
                className={`${styles.form__edit} ${editing && styles.editing}`}
                onClick={() => setEditing(!editing)}
              >
                <EditIcon />
              </span>
            )}
          </div>
          <RoleForm
            disabled={!editing}
            data={data}
            options={formOptions}
            onSubmitRoles={(value) => setEditing(!value)}
            mode={mode}
          />
        </div>
      )}
    </div>
  );
};

export default RolesItem;
