import { inject, observer } from 'mobx-react';
import React, { PureComponent } from 'react';
import { formatMoneyDetailedNode } from 'utils/ui/formatMoneyDetailedNode';
import { GraphStoreType } from '../../../../../../stores/graph/graphStore';
import { ReactComponent as NoParamIcon } from '../Criteria/assets/noParam.svg';
import { ReactComponent as YesParamIcon } from '../Criteria/assets/yesParam.svg';
import styles from './Parameters.module.scss';

interface Parameter {
  name: string;
  value: string;
  valueType: string;
}

function parseValue(parameter: Parameter) {
  switch (parameter.valueType) {
    case 'BOOLEAN':
      return ['true', '1'].includes(parameter.value) ? <YesParamIcon /> : <NoParamIcon />;
    case 'FLOAT':
      return formatMoneyDetailedNode(parameter.value);
    default:
      return parameter.value;
  }
}

type ParametersProps = {
  graphStore?: GraphStoreType;
  parameter: Parameter;
};

type ParametersState = {
  nodeData: any;
};

/**
 * Компонент Параметр критерия
 */
@inject('graphStore')
@observer
class Parameters extends PureComponent<ParametersProps, ParametersState> {
  render() {
    const {
      props: { parameter },
    } = this;

    return (
      <div className={styles.parameter}>
        <div>{parameter.name}</div>
        {parseValue(parameter) || '-'}
      </div>
    );
  }
}

export default Parameters;
